import React, { useEffect } from 'react';
import Scrollspy from 'react-scrollspy'

export default function Navbar() {
    useEffect(() => {
        var sticky = navbar.offsetTop;
        window.onscroll = function () {
            var navbar = document.getElementById("navbar");

            console.log('stick')

            if (window.pageYOffset >= sticky) {
                console.log('stick')
                navbar.classList.add("sticky")
            } else {
                console.log('dont stick')
                navbar.classList.remove("sticky");
            }
        }
    }, []);

    const gotoPage = (pageId) => {
        var elmnt = document.getElementById(pageId);
        elmnt.scrollIntoView({ behavior: 'smooth' });
    }
    return (
        <div className="cont" id="aboutP">
            <div className="navbar" id="navbar">
                <div className="inside">
                    <div>
                        <img className="logo" src={"https://afaithraf.github.io/my-portfolio/assets/logo.png"} alt="ashraf logo" />
                    </div>
                    <div className="navigation">
                            
  <Scrollspy items={ ['welcomeP', 'aboutP', 'workP', 'contactP'] } currentClassName="is-current">
    <li><a href="#welcomeP">Welcome</a></li>
    <li><a href="#aboutP">About me</a></li>
    <li><a href="#workP">My Work</a></li>
    <li><a href="#contactP">Contact</a></li>
  </Scrollspy>
                    </div>
                </div>
            </div>
            <style jsx>{`
            .cont{
            height: 84px;
            }
                .navbar{
                    left: 0;
                    right: 0;
                    background: #111111b3;
                   backdrop-filter: blur(25px);
                    top: 0;
                    color: #f1f1f1;
                    font-weight: 500;
                    z-index: 1;
                    border-top: 1px solid #ffffff38;
                    border-bottom: 1px solid #ffffff38;
                }
                .navbar.sticky{
                    position: fixed;
                }
                .inside{
                    display: flex;
                    flex-direction: row;
                    margin: 0 10vw;
                }
                .logo{
                    margin: 24px 0;
                    height: 30px;
                    transition-duration: 300ms;
                }
                .navbar.sticky .logo{
                    margin: 14px 0;
                    height: 20px;
                }
                .navigation{
                    display: flex;
                    margin-left: auto;
                }
                .navigation>ul{
                    display: flex;
                    padding: 0;
                    margin: auto;
                }
                .navigation li{
                    list-style: none;
                    padding: 10px 0;
                    margin: 0;
                    width: 120px;
                    text-align: center;
                    cursor: pointer;
                }     
                .navigation li a{
                    text-decoration: none;
                    color: #ffffff;
                }
            `}</style>
        </div>
    )
}
